/* portfolio.css */

.portfolio-container {
    position: relative;
    min-height: 100vh;
    padding: 2rem 0;
  }
  
  .dark-mode {
    background-color: var(--dark-bg);
    color: var(--dark-text-primary);
  }
  
  .light-mode {
    background-color: var(--light-bg);
    color: var(--light-text-primary);
  }
  
  .portfolio-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
  }
  
  /* Header Section */
  .section-header {
    text-align: center;
    margin-bottom: 3rem;
  }
  
  .section-title {
    font-size: 3rem;
    margin-bottom: 1rem;
    color: #6d1f7e;
    font-weight: 700;
    font-family: 'Montserrat', 'Inter', 'Segoe UI', sans-serif;
  }
  
  .section-divider {
    width: 60px;
    height: 4px;
    background-color: #6d1f7e;
    margin: 0 auto 2rem;
    border-radius: 2px;
  }
  
  .section-description {
    font-size: 1.1rem;
    max-width: 700px;
    margin: 0 auto;
    line-height: 1.8;
  }
  
  .dark-mode .section-description {
    color: var(--dark-text-secondary);
  }
  
  .light-mode .section-description {
    color: var(--light-text-secondary);
  }
  
  /* Filters and Search */
  .filters-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .category-filters {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
  }
  
  .filter-button {
    border: none;
    border-radius: 30px;
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .filter-button.active {
    background-color: #6d1f7e;
    color: white;
  }
  
  .dark-mode .filter-button:not(.active) {
    background-color: rgba(30, 30, 30, 0.5);
    color: var(--dark-text-primary);
  }
  
  .dark-mode .filter-button:not(.active):hover {
    background-color: rgba(40, 40, 40, 0.7);
  }
  
  .light-mode .filter-button:not(.active) {
    background-color: rgba(240, 240, 240, 0.7);
    color: var(--light-text-primary);
  }
  
  .light-mode .filter-button:not(.active):hover {
    background-color: rgba(230, 230, 230, 0.9);
  }
  
  /* Search Bar */
  .search-container {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
  
  .search-wrapper {
    position: relative;
    width: 100%;
    max-width: 500px;
  }
  
  .search-input {
    width: 100%;
    padding: 0.75rem 1rem 0.75rem 3rem;
    border-radius: 8px;
    font-size: 0.95rem;
    outline: none;
    transition: all 0.3s ease;
  }
  
  .dark-mode .search-input {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background-color: rgba(30, 30, 30, 0.5);
    color: var(--dark-text-primary);
  }
  
  .light-mode .search-input {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: rgba(255, 255, 255, 0.7);
    color: var(--light-text-primary);
  }
  
  .search-icon {
    position: absolute;
    left: 1rem;
    top: 50%;
    transform: translateY(-50%);
    color: #6d1f7e;
  }
  
  /* Projects Grid */
  .projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 2rem;
    margin-bottom: 3rem;
  }
  
  /* Project Card */
  .project-card {
    border-radius: 16px;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    position: relative;
    animation: fadeIn 0.6s ease-out;
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
  }
  
  .dark-mode .project-card {
    background-color: rgba(15, 15, 15, 0.6);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .light-mode .project-card {
    background-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.05);
    border: 1px solid rgba(0, 0, 0, 0.05);
  }
  
  .project-card:hover {
    transform: translateY(-8px);
  }
  
  .dark-mode .project-card:hover {
    box-shadow: 0 16px 48px rgba(0, 0, 0, 0.3);
  }
  
  .light-mode .project-card:hover {
    box-shadow: 0 16px 48px rgba(0, 0, 0, 0.1);
  }
  
  /* Featured Badge */
  .featured-badge {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background-color: #6d1f7e;
    color: white;
    padding: 0.3rem 0.8rem;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: 500;
    z-index: 1;
  }
  
  /* Project Image */
  .project-image-container {
    width: 100%;
    height: 200px;
    overflow: hidden;
    position: relative;
  }
  
  .project-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.6s ease;
  }
  
  .dark-mode .project-image {
    filter: brightness(0.85);
  }
  
  .light-mode .project-image {
    filter: brightness(1);
  }
  
  .project-image-overlay {
    position: absolute;
    inset: 0;
    background-color: rgba(109, 31, 126, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .project-card:hover .project-image-overlay {
    opacity: 1;
  }
  
  .view-details-button {
    background-color: #6d1f7e;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    font-size: 0.9rem;
  }
  
  /* Project Info */
  .project-info {
    padding: 1.5rem;
  }
  
  .project-title {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
    font-weight: 600;
  }
  
  .dark-mode .project-title {
    color: var(--dark-text-primary);
  }
  
  .light-mode .project-title {
    color: var(--light-text-primary);
  }
  
  .project-description {
    font-size: 0.95rem;
    margin-bottom: 1.2rem;
    line-height: 1.6;
  }
  
  .dark-mode .project-description {
    color: var(--dark-text-secondary);
  }
  
  .light-mode .project-description {
    color: var(--light-text-secondary);
  }
  
  /* Category Tag */
  .category-tag-container {
    margin-bottom: 1rem;
  }
  
  .category-tag {
    display: inline-block;
    background-color: rgba(109, 31, 126, 0.1);
    color: #6d1f7e;
    padding: 0.3rem 0.8rem;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: 500;
  }
  
  /* Technologies */
  .tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
  }
  
  .tech-tag {
    display: inline-block;
    padding: 0.2rem 0.6rem;
    font-size: 0.75rem;
    border-radius: 50px;
  }
  
  .dark-mode .tech-tag {
    background-color: rgba(40, 40, 40, 0.6);
    color: #cbd5e1;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .light-mode .tech-tag {
    background-color: rgba(240, 240, 240, 0.6);
    color: #6d1f7e;
    border: 1px solid rgba(0, 0, 0, 0.05);
  }
  
  /* No Projects Found */
  .no-projects {
    grid-column: 1 / -1;
    text-align: center;
    padding: 3rem;
    border-radius: 16px;
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
  }
  
  .dark-mode .no-projects {
    background-color: rgba(15, 15, 15, 0.6);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  }
  
  .light-mode .no-projects {
    background-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.05);
  }
  
  .no-projects-title {
    font-size: 1.25rem;
    margin-bottom: 1rem;
  }
  
  .dark-mode .no-projects-title {
    color: var(--dark-text-primary);
  }
  
  .light-mode .no-projects-title {
    color: var(--light-text-primary);
  }
  
  .no-projects-description {
    margin-bottom: 1rem;
  }
  
  .dark-mode .no-projects-description {
    color: var(--dark-text-secondary);
  }
  
  .light-mode .no-projects-description {
    color: var(--light-text-secondary);
  }
  
  .reset-button {
    margin-top: 1rem;
    background-color: #6d1f7e;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.6rem 1.2rem;
    cursor: pointer;
  }
  
  /* Project Detail Modal */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(5px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    padding-top: 5rem;
    z-index: 1000;
    animation: fadeIn 0.3s ease-out;
  }
  
  .modal-container {
    border-radius: 16px;
    width: 100%;
    max-width: 800px;
    max-height: 85vh;
    overflow: auto;
    position: relative;
    animation: scaleIn 0.3s ease-out;
    margin-top: 30px;
  }
  
  .dark-mode .modal-container {
    background-color: var(--dark-bg);
  }
  
  .light-mode .modal-container {
    background-color: var(--light-bg);
  }
  
  .modal-close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    cursor: pointer;
    z-index: 10;
    transition: all 0.3s ease;
  }
  
  .dark-mode .modal-close-button {
    background-color: rgba(30, 30, 30, 0.8);
  }
  
  .light-mode .modal-close-button {
    background-color: rgba(240, 240, 240, 0.8);
  }
  
  .modal-close-button:hover {
    background-color: #6d1f7e;
  }
  
  .modal-close-button:hover svg {
    color: white !important;
  }
  
  .modal-image {
    width: 100%;
    height: 275px;
    overflow: hidden;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
  }
  
  .modal-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .modal-content {
    padding: 1.75rem;
  }
  
  .modal-title {
    font-size: 1.7rem;
    margin-bottom: 0.9rem;
    font-weight: 600;
  }
  
  .dark-mode .modal-title {
    color: var(--dark-text-primary);
  }
  
  .light-mode .modal-title {
    color: var(--light-text-primary);
  }
  
  .modal-tags {
    margin-bottom: 1.4rem;
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .modal-description {
    font-size: 1.03rem;
    margin-bottom: 1.75rem;
    line-height: 1.65;
  }
  
  .dark-mode .modal-description {
    color: var(--dark-text-secondary);
  }
  
  .light-mode .modal-description {
    color: var(--light-text-secondary);
  }
  
  .modal-tech-section {
    margin-bottom: 1.75rem;
  }
  
  .modal-tech-title {
    font-size: 1.15rem;
    margin-bottom: 0.9rem;
    font-weight: 600;
  }
  
  .dark-mode .modal-tech-title {
    color: var(--dark-text-primary);
  }
  
  .light-mode .modal-tech-title {
    color: var(--light-text-primary);
  }
  
  .modal-tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.7rem;
  }
  
  .modal-tech-tag {
    display: inline-block;
    padding: 0.35rem 0.75rem;
    font-size: 0.83rem;
    border-radius: 50px;
  }
  
  .dark-mode .modal-tech-tag {
    background-color: rgba(40, 40, 40, 0.6);
    color: #cbd5e1;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .light-mode .modal-tech-tag {
    background-color: rgba(240, 240, 240, 0.6);
    color: #6d1f7e;
    border: 1px solid rgba(0, 0, 0, 0.05);
  }
  
  .modal-links {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .github-link {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.15rem;
    border-radius: 8px;
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    font-size: 0.95rem;
  }
  
  .dark-mode .github-link {
    background-color: rgba(30, 30, 30, 0.8);
    color: var(--dark-text-primary);
  }
  
  .light-mode .github-link {
    background-color: rgba(240, 240, 240, 0.8);
    color: var(--light-text-primary);
  }
  
  .github-link:hover {
    background-color: #333;
    color: white;
  }
  
  .demo-link {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    background-color: #6d1f7e;
    color: white;
    padding: 0.75rem 1.15rem;
    border-radius: 8px;
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    font-size: 0.95rem;
  }
  
  .demo-link:hover {
    background-color: #8f3ba0;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes scaleIn {
    from { transform: scale(0.9); opacity: 0; }
    to { transform: scale(1); opacity: 1; }
  }
  
  /* Media Queries */
  @media (max-width: 768px) {
    .projects-grid {
      grid-template-columns: 1fr;
    }
    
    .section-title {
      font-size: 2.5rem;
    }
    
    .modal-content {
      padding: 1.5rem;
    }
    
    .modal-title {
      font-size: 1.5rem;
    }
  }